import React, { useEffect, useState } from 'react';
import styles from './index.css';
import PropTypes from 'prop-types';
import ShowImg from '@src/mobile/priceList/layout/component/box/showImg';

/* eslint-disable */
const getBaseUrl = (env) => {
  switch (env) {
      case 'development':
          return `${window.location.origin}/mobile/news/loc/redEnvelopesLottery/rules`;
      case 'staging':
          return 'http://test.news.ifeng.com/loc/redEnvelopesLottery/rules';
      case 'production':
          // return 'http://news.ifeng.com';
          return `${window.location.origin}/redEnvelopesLottery/mobile_rules.html`;
      default:
          break;
  }
};
// webpack全局注入
const baseUrl = getBaseUrl(NODE_ENV);

const backIcon = 'https://x0.ifengimg.com/ucms/2022_04/CFEE4727CFF72403903CFF27C59616A86C7365F2_size0_w60_h60.png';

const Header = (props) => {
  const [height, setHeight] = useState(0);

  useEffect(() => {
    if (window.grounds) {
      const isAndroid = /android/i.test(window.grounds.getDeviceInfo().getOs());
      const ratio = isAndroid ? devicePixelRatio : 1;
      const statusBarHeight = window.grounds.getStatusBarHeight() / ratio; // 设备状态栏的高度

      setHeight(statusBarHeight);
    }

  }, []);
  const backFn = () => {
    window.grounds.close();
  };
  const goToRules = () => {

    // const url = `${window.location.origin}/mobile/news/loc/redEnvelopesLottery/rules`;
    const url = baseUrl;

    // 端外跳转
    window.location.href = url;
    // window.grounds.dispatch('web', 'https://news.ifeng.com/loc/redEnvelopesLottery/rules?isFull=0&immersion=true');
  };

return (
      <div>
        <div style={{ paddingTop: `${height}px` }} className={styles.headerTop}>
          <img onClick={backFn} src={backIcon} className={styles.headerTopImg} />
          <div className={styles.headerTopText}>我的奖品</div>
        </div>
        {props.showRules && <div onClick={goToRules} className={styles.header}>

          <div className={styles.headerText}>查看兑奖攻略</div>
          <div className={styles.headerRight} style={{ color: 'white' }} />

        </div>}

      </div>

  );
};

Header.propTypes = {
  showRules: PropTypes.boolean,
};
export default Header;
