import React, { useState } from 'react';
import styles from './index.css';
import copy from 'copy-to-clipboard';
import BlackBox from '@src/mobile/priceList/layout/component/box/blackBox';
import ShowImg from '@src/mobile/priceList/layout/component/box/showImg';
import PropTypes from 'prop-types';
// 一共三种类型：
// 1 红包封面
// 2 已经兑换红包封面
// 3 手机屏幕
const copyImg = 'https://x0.ifengimg.com/ucms/2022_03/FC9E26B1C8D45C498C6F97B2A4590B6AD496EFBC_size2_w142_h54.png';
const showImgIcon = 'https://x0.ifengimg.com/ucms/2022_03/FBC3A80F79BE643CF787FA36FFDD5154B2A3660D_size1_w142_h54.png';
const Box = (props) => {
  const [showBlackBox, setShowBlackBox] = useState(0);
  const [showImg, setShowImg] = useState(false);

  console.log(props.item);
  const handleCopyUrl = code => {
    copy(code);
    setShowBlackBox(1);
    setTimeout(() => {
      setShowBlackBox(0);
    }, 3000);
  };
  const downImg = () => {
    setShowImg(true);
  };

return (
      <div>
        <div className={styles.container}>
          {<div className={`${styles.box} ${props.item.status === 2 ? styles.gary : ''}`}>
            <div className={styles.boxImg}>
              <img className={props.item.lotttype === 2 ? styles.boxImgB : styles.boxImgL} src={props.item.img} />
            </div>
            <div className={styles.boxRight}>
              <div className={styles.boxRightTitle}>{props.item?.title}</div>
              <div className={styles.codeBox}>
                {props.item?.exchcode ? <div className={`${styles.code} ${props.item.status === 2 ? styles.deleteLine : ''}`}>
                  <div className={styles.ff}>兑换码：</div>
                  <div className={styles.ff}>{props.item?.exchcode}</div>
                </div> : <div className={`${styles.code}`}>数量：{props.item?.prizenum}</div>}
              </div>
              <div className={`${styles.code}`}>
                {props.item?.exchcode ? '兑换' : '下载'}截止时间: {props.item?.endtime}
              </div>
              <div style={{ display: 'flex', justifyContent: 'flex-end' }}>
                {props.item?.exchcode ?
                    <div>
                      { props.item?.status === 2 ? <img className={styles.copyImg} src="https://x0.ifengimg.com/ucms/2022_03/65FA041DA0F097D9E39013BDC64243691CA57D49_size1_w142_h54.png" /> : <img
                          className={styles.copyImg} src={copyImg} onClick={() => {
                        handleCopyUrl(props.item?.exchcode);
                      }} />}
                    </div>
                  : <img
                          className={styles.copyImg}
                          src={showImgIcon} onClick={() => {
  downImg();
}} />}
              </div>

            </div>

          </div>}

        </div>
        {showBlackBox !== 0 && <BlackBox type={showBlackBox} />}
        {showImg && <ShowImg close={setShowImg} imgSrc={props?.item?.img} />}
      </div>
  );
};

Box.propTypes = {
  item: {
    exchcode: PropTypes.string,
    endtime: PropTypes.string,
    img: PropTypes.string,
    status: PropTypes.number,
    title: PropTypes.string,
    lotttype: PropTypes.number
  },
};
export default Box;
